@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
.footer {
  background-color: #333;
  display: flex;
  height: 20vh;
  align-items: center;
  justify-content: center;
  grid-gap: 100px;
  gap: 100px;
}

a {
  color: white;
  transition: all 0.3s;
  font-size: 32px;
}

a:hover {
  color: #3498db;
  transform: scale(1.12);
}

.search-box-container {
  display: flex;
  height: 20vh;
  align-items: center;
  justify-content: center;
}

.search-box {
  padding: 15px;
  border: none;
  border-radius: 12px;
  box-shadow: 0 6px 30px -1px rgb(0 0 0 / 60%);
  width: 400px;
  outline: none;
  font-family: 'Montserrat', sans-serif;
}

@media screen and (max-width: 600px) {
  .search-box {
    width: 250px;
  }
}

.top {
  height: 20vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.titles {
  line-height: 12px;
  text-align: center;
}

h1,
p {
  font-family: 'Montserrat', sans-serif;
}

.title {
  font-size: 55px;
}

p {
  font-style: italic;
  font-size: 22px;
}

@media screen and (max-width: 600px) {
  .title {
    font-size: 32px;
  }

  p {
    font-size: 10px;
  }
}

.weather-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 40vh;
  line-height: 10px;
}

h1 {
  font-size: 32px;
}

h2 {
  font-style: italic;
  font-size: 32px;
}

@media screen and (max-width: 600px) {
  h1 {
    font-size: 20px;
  }

  h2 {
    font-size: 20px;
  }
}

body {
  background: linear-gradient(to right, #373b44, #4286f4);
  color: white;
}

.App {
  height: 100vh;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

